<template>
    <div class="integral-style-one">
        <a class="img" href="#" @click="goHome" :style="{ background:'url('+data.img+') no-repeat center' } " v-if="data.img"></a>
        <a class="img no-img" href="#" @click="goHome" v-else></a>
        <p class="title">{{ data.name }}</p>
        <p class="money">{{ data.money }}+{{ data.integral }}{{ $L('积分') }}</p>
        <div class="bottom">
            <el-button type="primary" size="small" v-if="data.Redemption === true && (data.inte_type == 1 || data.inte_type == 2)" @click="exchange(data.inte_id,data.type)">{{ $L('立即兑换') }}</el-button>
            <el-button type="primary" size="small" v-else-if="data.Redemption === true && (data.inte_type == 3)" @click="exchangeCoupon(data.inte_id,data.product_id,data.type)">{{ $L('立即兑换') }}</el-button>
            <el-button type="info" size="small" disabled v-else>{{ $L('立即兑换') }}</el-button>
        </div>
    </div>
</template>

<script>
    import exchangeBox from './exchangeBox.vue';
    import exchangeCoupon from './exchangeCoupon.vue';
    export default {
        name: "integralStyleOne",
        components:{
            exchangeBox,
            exchangeCoupon
        },
        props:{
            data:{
                type: Object,
                default :()=>{
                    return {};
                }
            }
        },
        methods:{
            exchange(id,type){
                this.$store.commit('setDialogObj', {
                    name: this.$L('立即兑换'),
                    view: exchangeBox,
                    width: '1000px',
                    height: '600px',
                    data: {
                        id:id,
                        type:type
                    }
                });
            },
            exchangeCoupon(inte_id, prodId,type){
                this.$store.commit('setDialogObj', {
                    name: this.$L('兑换优惠卷'),
                    view: exchangeCoupon,
                    width: '600px',
                    height: '400px',
                    data: {
                        inte_id:inte_id,
                        prodId:prodId,
                        type:type
                    }
                });
            },
            goHome(){
                this.$gotoWebHome();
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/common/common";
    .integral-style-one{
         font-size: 14px;
         .img{
             display: block;
             color: #666;
             width: 100px;
             height: 100px;
             margin: 0 auto;
             background-size: 100% 100% !important;
         }
        .no-img{
            background: url("../../assets/img/common/defaultimg.jpg") no-repeat center;
        }
         .title{
             text-overflow: ellipsis;
             white-space: nowrap;
             overflow: hidden;
             text-align: center;
             padding-top: 10px;
         }
         .money{
             color: #000;
             text-align: center;
             padding-top: 5px;
         }
         .bottom{
             text-align: center;
             margin-top: 15px;
             button{
                 padding: 10px 20px;
             }
         }
 }
</style>